import React, { Component } from "react";
import { Typography } from "libs-player-ui-components";
import textFit from "textfit";
import { withTheme } from "styled-components";
import CardModel from "../../model/card-model";

interface ITitleViewer {
    isScalingActive: boolean;
    cardModel: CardModel;
    theme: {
        title: {
            fontColor: string;
        };
    };
}
class TitleViewer extends Component<ITitleViewer> {

    ref;

    componentDidMount() {
        if (this.props.isScalingActive) {
            textFit(this.ref.current, {
                minFontSize: 25.6,
                maxFontSize: 30
              });
        }

    }

    render() {
        this.ref = React.createRef();
        return (
            <div ref={this.ref} data-tid={'container-title-text'}>
                <Typography variant="h2" color={this.props.theme.title.fontColor}>{this.props.cardModel.extractTitleFromJSON()}</Typography>
            </div>
        )
    }
    
    getHeight() {
        return getComputedStyle(this.ref.current).height;
    }
}


export default withTheme(TitleViewer);