import React, { Component } from "react";
import Card from "../components/card";
import ImageViewer from "../components/image-viewer";
import CardModel from "../model/card-model";
import TitleViewer from "../components/title-viewer";

interface IMediaOnlyTemplate {
    cardModel: CardModel,
    initOptions: {
        isScalingActive: boolean
    }
};

export default class MediaOnlyTemplate extends Component<IMediaOnlyTemplate> {

    constructor(props) {
        super(props);
    }

    render() {
        let isTitlePresent = this.props.cardModel.isTitlePresent();
        return (<Card>
            {isTitlePresent &&
                <TitleViewer cardModel={this.props.cardModel} isScalingActive={this.props.initOptions.isScalingActive}/>
            }
            <ImageViewer imageURL={this.props.cardModel.getMediaData()} height={'100%'} isMarginRequired={isTitlePresent}></ImageViewer>
        </Card>);
    }
}