import React from "react";
import styled from "styled-components";
import { generateMarginFromViewportHeight } from "../../utils/utility";


interface ImageViewerProps {
    imageURL: string;
    height?: string;
    isMarginRequired?: boolean;
}

const Image = styled.img<ImageViewerProps>`
background: ${props => props.theme.bgColor? props.theme.bgColor: 'white'};
draggable: false;
max-height: 100%;
max-width: 100%;
content: url(${props => props.imageURL});
`;

const ImageViewer = React.forwardRef((props: ImageViewerProps, ref: React.RefObject<HTMLImageElement>) => {
    let requiredMargin = generateMarginFromViewportHeight();
    let marginTop = props.isMarginRequired ? requiredMargin : '0px';
    let containerHeight = props.height && 'calc('+ props.height + ' - ' + marginTop + ')';
    return (<div ref={ref} style={{height: containerHeight, marginTop: marginTop, textAlign: 'center'}} data-tid={'image-thumbnail'}>
        <Image imageURL={props.imageURL}></Image>
    </div>)
});

export default ImageViewer;