import ReactDOM, { render } from "react-dom";
import { CardTemplateFactory } from "./template/cardTemplate.factory";
import React from "react";
import { ThemeProvider } from "styled-components";
import { ThemeProvider as LPUCThemeProvider } from "libs-player-ui-components";
import { IntlProvider } from "react-intl";
import CardModel from "./model/card-model";
import { IItem, LaunchModes, IOptions, IDimensions, IScore } from "libs-interface-item-player/src/interface-item";
import { PresentationTheme } from "./theme/presentation-card-player.default-theme";
import { LibsPlayerUiComponentTheme } from "./theme/libs-player-ui-component.theme";
import './styles/index.less';

export default class FlashCardPlayer implements IItem {

  cardModel: CardModel;
  pcpVersion: string;
  container: any;

  constructor() {
    this.pcpVersion = process.env.pcpVersion;
  }

  /**
   * Method to initialize the presentation card item player
   * @param uid 
   * @param itemJSON 
   * @param container 
   * @param initOptions 
   */
  initItem(uid: string, itemJSON: any, container: any, initOptions: any) {
    try {
      this.cardModel = new CardModel(itemJSON);
      this.container = container;
      initOptions['isScalingActive'] = true;
      ReactDOM.render(
        <IntlProvider locale={navigator.language} defaultLocale="en-US">
          <ThemeProvider theme={PresentationTheme}>
            <LPUCThemeProvider theme={LibsPlayerUiComponentTheme}>
              <div pcp-version={this.pcpVersion} data-tid={'container-presentation-card-player'} className={'presentation-card-container'}>
              {CardTemplateFactory.getTemplate(uid, itemJSON, this.cardModel, container, initOptions)}
              </div>
            </LPUCThemeProvider>
          </ThemeProvider>
        </IntlProvider>

        , container);
    } catch (e) {

    }
  }
  canShowAnswer(): boolean {
    throw new Error("Method not implemented.");
  }

  /**
   * Function to destroy the presentation card item player and remove the instance of cardModel from the memory.
   */
  destroy(): void {
    this.cardModel = null;
    delete this.cardModel;
    const Nothing = () => null;
    render(<Nothing />, this.container);
  }

  focalise(): void {
    throw new Error("Method not implemented.");
  }
  getOptimumDimensions(): IDimensions {
    throw new Error("Method not implemented.");
  }
  getScore(): IScore {
    throw new Error("Method not implemented.");
  }
  getState() {
    return {};
  }
  hasHints(): boolean {
    return false;
  }
  hasUserResponse(): boolean {
    return false;
  }
  hide(): void {
    throw new Error("Method not implemented.");
  }
  hideAnswer(): void {
    throw new Error("Method not implemented.");
  }
  hideHints(): void {
    throw new Error("Method not implemented.");
  }
  lock(): void {
    throw new Error("Method not implemented.");
  }
  redo(options: any): void {
    throw new Error("Method not implemented.");
  }
  remainingHints(): number {
    throw new Error("Method not implemented.");
  }
  resetDimensions(): void {
    throw new Error("Method not implemented.");
  }
  setState(state: any, launchMode: LaunchModes): void {
    throw new Error("Method not implemented.");
  }
  show(): void {
    throw new Error("Method not implemented.");
  }
  showAnswer(): void {
    throw new Error("Method not implemented.");
  }
  showHints(): number {
    throw new Error("Method not implemented.");
  }
  subscribe(eventName: string, eventHandler: Function): void {
    throw new Error("Method not implemented.");
  }
  unlock(): void {
    throw new Error("Method not implemented.");
  }
  unsubscribe(eventName: string, eventHandler: Function): void {
    throw new Error("Method not implemented.");
  }
  validateResponse(options?: IOptions): boolean {
    throw new Error("Method not implemented.");
  }
}