import React, { Component } from "react";
import Card from "../components/card";
import XHtmlViewer from "../components/text-renderer";
import CardModel from "../model/card-model";
import TitleViewer from "../components/title-viewer";

interface ITextOnlyTemplate {
    cardModel: CardModel,
    initOptions: {
        isScalingActive: boolean
    }
};

interface ITextOnlyState {
    xhtmlViewerHeight: any;
}

class TextOnlyTemplate extends Component<ITextOnlyTemplate, ITextOnlyState> {

    titleRef;
    isTitlePresent: boolean;

    constructor(props) {
        super(props);
        this.state = {
            xhtmlViewerHeight: 0
        };
    }

    componentDidMount() {
        this.isTitlePresent && this.setState({xhtmlViewerHeight: this.titleRef.current.getHeight()});
    }
    
    render() {
        this.isTitlePresent = this.props.cardModel.isTitlePresent();
        this.titleRef = React.createRef();
        return (<Card>
            { this.isTitlePresent &&
                <TitleViewer ref={this.titleRef} cardModel={this.props.cardModel} isScalingActive={this.props.initOptions.isScalingActive}/>}
                <XHtmlViewer cardModel={this.props.cardModel} isScalingActive={this.props.initOptions.isScalingActive} height={this.state.xhtmlViewerHeight} isMarginRequired={this.isTitlePresent}></XHtmlViewer>
        </Card>)
    }
}

export default TextOnlyTemplate;