import React, { Component } from "react";
import textFit from "textfit";
import CardModel from "../../model/card-model";
import { Typography, QuestionText, Color } from "libs-player-ui-components";
import { withTheme } from "styled-components";
import "../../styles/index.less";
import { generateMarginFromViewportHeight } from "../../utils/utility";

type fontColorType =  {
    h2?: Color;
    li?:Color;
  }
interface IXhtmlViewer {
    cardModel: CardModel;
    isScalingActive: boolean;
    isMarginRequired?: boolean;
    height?: string;
    theme: {
        fontColors: fontColorType;
    };
}
class XHtmlViewer extends Component<IXhtmlViewer> {

    ref;
    cardModel: CardModel;

    constructor(props) {
        super(props);
        this.cardModel = this.props.cardModel;
    }

    componentDidMount() {
        this.scaleText();
    }

    componentDidUpdate() {
        this.scaleText();
    }

    render() {
        let requiredMargin = generateMarginFromViewportHeight();
        let marginTop = this.props.isMarginRequired ? requiredMargin : '0px';
        let height = this.props.height ? this.props.height : '0px';
        let containerHeight = 'calc(100% - ' + marginTop + ' - ' + height + ')';
        this.ref = React.createRef();
        return (
            <div className={'xhtml-viewer-container'} data-tid={'text-content'} ref={this.ref} style={{marginTop: marginTop, height: containerHeight}}>{this.getTextContent()}</div>
        )
    }

    private getTextContent() {
        let textContentType = this.cardModel.getTextContentType();
        let textContent = this.cardModel.getTextContentData();
        if (textContentType == 'plain-text') {
            return (<Typography variant='h3'>{textContent}</Typography>);
        } else {
            return (<QuestionText questionText={textContent} backgroundColor={'background'} padding={0} fontColors={this.props.theme.fontColors}></QuestionText>);
        }
    }

    private scaleText() {
        if (this.props.isScalingActive) {
            textFit(this.ref.current, {
                maxFontSize: 24
              });
        }
    }
}

export default withTheme(XHtmlViewer);