import React, { Component } from "react";
import Card from "../components/card";
import ImageViewer from "../components/image-viewer";
import XHtmlViewer from "../components/text-renderer";
import CardModel from "../model/card-model";
import TitleViewer from "../components/title-viewer";
import { withTheme } from "styled-components";
import textFit from "textfit";
import { generateMarginFromViewportHeight } from "../utils/utility";

interface IMediaTextTopBottomTemplate {
    cardModel: CardModel;
    initOptions: {
        isScalingActive: boolean;
    };
    theme: {
        title: {
            height: string;
        };
    }
}

interface IMediaTextTopBottomState {
    imageTextContainer: {
        height: string;
    },
    textContainer: {
        height: string;
    }
}

class MediaTextTopBottomTemplate extends Component<IMediaTextTopBottomTemplate, IMediaTextTopBottomState> {

    titleRef;
    imageContainerRef;

    constructor(props) {
        super(props);
        this.state = {
            imageTextContainer: {
                height: '100%'
            },
            textContainer: {
                height: '0px'
            }
        }
    }

    componentDidMount() {
        this.updateMediaTextContainer();
    }

    render() {
        this.titleRef = React.createRef();
        this.imageContainerRef = React.createRef();
        let isTitlePresent = this.props.cardModel.isTitlePresent();
        return (<Card >
            {isTitlePresent &&
            <TitleViewer ref={this.titleRef} cardModel={this.props.cardModel} isScalingActive={this.props.initOptions.isScalingActive}/>}
            <div style={{height: this.state.imageTextContainer.height}}>
                <ImageViewer ref= {this.imageContainerRef} imageURL={this.props.cardModel.getMediaData()} isMarginRequired={isTitlePresent}></ImageViewer>
                <XHtmlViewer cardModel={this.props.cardModel} height={this.state.textContainer.height} isScalingActive={this.props.initOptions.isScalingActive} isMarginRequired={true}></XHtmlViewer>
            </div>
        </Card>);
    }

    private updateMediaTextContainer() {
        let titleContainerHeight = this.titleRef.current && this.titleRef.current.getHeight();
        let requiredMargin = generateMarginFromViewportHeight();
        let mediaTextContainerHeight = titleContainerHeight ? 'calc(100% - ' + titleContainerHeight + ' - ' + requiredMargin + ')' : '100%';
        this.setState({imageTextContainer: {height: mediaTextContainerHeight}}, () => {
            this.updateTextContainer();
        });        
    }

    private updateTextContainer() {
        let imageContainerHeight = getComputedStyle(this.imageContainerRef.current).height;
        this.setState({textContainer: {height: imageContainerHeight}});
        
    }
}

export default withTheme(MediaTextTopBottomTemplate);
