import React from "react";
import TextOnlyTemplate from "./textOnly.template";
import MediaOnlyTemplate from "./mediaOnly.template";
import MediaTextTopBottomTemplate from "./mediaText-topBottom.template";
import MediaTextBottomTopTemplate from "./mediaText-bottomTop.template";
import CardModel from "../model/card-model";

export class CardTemplateFactory {

    private static TemplateMap: any = {
        "text-only": TextOnlyTemplate,
        "media-only": MediaOnlyTemplate,
        "media-text": {
            "top-bottom": MediaTextTopBottomTemplate,
            "bottom-top": MediaTextBottomTopTemplate
        }
    }

    static getTemplate(uid: string, itemJSON: any, cardModel: CardModel, container: HTMLElement, initOptions: any) {
        let templateName = cardModel.getTemplateType();
        let Template;
        if (templateName == 'media-text') {
            let templateLayout = cardModel.getTemplateLayout();
            Template = CardTemplateFactory.TemplateMap[templateName][templateLayout];
        } else {
            Template = CardTemplateFactory.TemplateMap[templateName];
        }
        return <Template uid={uid} itemJSON={itemJSON} cardModel={cardModel} container={container} initOptions={initOptions}></Template>;
    }
}