import Card from "../components/card";
import React, { Component } from "react";
import ImageViewer from "../components/image-viewer";
import XHtmlViewer from "../components/text-renderer";
import CardModel from "../model/card-model";
import TitleViewer from "../components/title-viewer";
import { withTheme } from "styled-components";
import { generateMarginFromViewportHeight } from "../utils/utility";
interface IMediaTextBottomTopTemplate {
    cardModel: CardModel;
    initOptions: {
        isScalingActive: boolean;
    };
    theme: {
        title: {
            height: string;
        };
    }
}

interface IMediaTextBottomState {
    textMediaContainer: {
        height: string;
    },
    textContainer: {
        height: string;
    }
}
class MediaTextBottomTopTemplate extends Component<IMediaTextBottomTopTemplate, IMediaTextBottomState> {

    titleRef;
    imageContainerRef;

    constructor(props) {
        super(props);
        this.state = {
            textMediaContainer: {
                height: '100%'
            },
            textContainer: {
                height: "0px"
            }
        }
    }

    componentDidMount() {
        this.updateTextMediaContainer();
    }

    render() {
        this.titleRef = React.createRef();
        this.imageContainerRef = React.createRef();
        let isTitlePresent = this.props.cardModel.isTitlePresent();
        return (
            <Card>
                {isTitlePresent &&
                    <TitleViewer ref={this.titleRef} cardModel={this.props.cardModel} isScalingActive={this.props.initOptions.isScalingActive}/>}
                <div style={{height: this.state.textMediaContainer.height}}>
                    <XHtmlViewer cardModel={this.props.cardModel} height={this.state.textContainer.height} isScalingActive={this.props.initOptions.isScalingActive}></XHtmlViewer>
                    <ImageViewer ref={this.imageContainerRef} imageURL={this.props.cardModel.getMediaData()} isMarginRequired={true}></ImageViewer>
                </div>
            </Card>
        )
    }

    private updateTextMediaContainer() {
        let titleContainerHeight = this.titleRef.current && this.titleRef.current.getHeight();
        let requiredMargin = generateMarginFromViewportHeight();
        let textMediaContainerHeight = titleContainerHeight ? 'calc(100% - ' + titleContainerHeight + ' - ' + requiredMargin + ')' : '100%';
        this.setState({textMediaContainer: {height: textMediaContainerHeight}}, () => {
            this.updateTextContainer();
        });        
    }

    private updateTextContainer() {
        let imageContainerHeight = getComputedStyle(this.imageContainerRef.current).height;
        let updatedHeight = parseFloat(imageContainerHeight) + parseFloat(generateMarginFromViewportHeight());
        this.setState({textContainer: {height: updatedHeight + 'px'}});
        
    }

}

export default withTheme(MediaTextBottomTopTemplate);